.footer {
  padding: 60px 0;
  background-color: #333333;
  text-align: center;
  color: white;
}
.footer__info {
  display: inline-block;
  vertical-align: top;
  width: 310px;
  text-align: left;
  font-size: 16px;
}
.footer__info p {
  margin: 5px 0;
}
.footer__link {
  display: inline-block;
  vertical-align: top;
  width: 310px;
  text-align: right;
  font-size: 50px;
  color: white;
}

@media (max-width: 650px) {
  .footer__info {
    display: block;
    margin: 0 auto;
  }
  .footer__link {
    display: block;
    margin: 20px auto;
    text-align: center;
  }
}


